/*  reset.css start */
html,body,h1,h2,h3,h4,h5,h6,div,dl,dt,dd,ul,ol,li,p,blockquote,pre,hr,figure,table,caption,th,td,form,fieldset,legend,input,button,textarea,menu {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption,th {
  text-align: left;
  font-weight: normal;
}

html,body,fieldset,img,iframe,abbr {
  border: 0;
}

i,cite,em,var,address,dfn {
  font-style: normal;
}

ul,ol,li {
  list-style: none;
}

a {
  text-decoration: none;
}

a,button {
  cursor: pointer;
}

img { 
  border: 0;
}

input,textarea {
  border: none;
  outline: none;
  -webkit-appearance: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}

textarea {
  overflow:auto;
  resize:none;
}

.fl {
  float:left;
}

.fr {
  float:right;
}

.clearfix {
  zoom:1;
}

.clearfix:after {
  content:'';
  display:block;
  clear:both;
  visibility:hidden;
  overflow:hidden;
}

.flex {
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
}

.flex_item {
  -webkit-box-flex:1;
  -webkit-flex:1;
  flex:1;
}

.ellipsis {
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow:hidden
}

.ellipsis_2 {
  display:-webkit-box !important;
  overflow:hidden;
  text-overflow:ellipsis;
  word-break:break-all;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}

body {
  background-color: #f5f5f5;
}
/*  reset.css end */

/* TopLayout */

.top-div {
  height: 64px;
  background-color: #fff;
  box-shadow: 5px 5px 5px #1919192b;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
}

.top-div-main {
  height: 64px;
  width: 1500px;
  margin: 0 auto;
  display: flex;
}

.logo-div {
  margin-left: 80px;
  flex-grow: 0;
  width: 270px;
}

.top-ul {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.top-ul li {
  flex-grow: 0;
  width: 130px;
  text-align: center;
  cursor: pointer;
  line-height: 60px;
  font-size: 16px;
  color: #546681;
  font-weight: 600;
  border-bottom: #fff solid 4px;
}

.top-ul li:hover {
  color: #0070e0;
  border-bottom: #0070e0 solid 4px;
}

.top-ul .current-li {
  color: #0070e0;
  border-bottom: #fff solid 4px;
}

/* main */
.main-div {
  min-height: calc(100vh - 196px);
  width: 1200px;
  margin: 64px auto 0 auto;
  padding: 16px;
  display: flex;
}

.main-div-left {
  width: 300px;
  margin-top: 10px;
}

.ant-anchor-link-title {
  height: 30px;
  line-height: 30px;
}

.main-div-right {
  width: 900px;
}

.main-div-right-item {
  line-height: 40px;
  width: 160px;
}

/* .main-div-right-item img {
  width: 20px;
  height: 20px;
  vertical-align: text-top;
  margin-right: 7px;
} */

.main-div-right-item .ant-image {
  margin-right: 7px;
}

.main-div-right-item .ant-image .ant-image-img {
  vertical-align: text-top;
}

/* bottomLayout */

.bottom-div {
  height: 100px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.bottom-div p {
  line-height: 35px;
}

.gt-container {
  text-align: left;
}

.ant-float-btn-primary .ant-float-btn-body {
  background: linear-gradient(to right, #348f50, #56b4d3);
}

/* mainPage */

.mainPageDiv {
  width: 100%;
  height: 100vh;
  background-image: url(./asset/img/bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

*:before, *:after {
  content: "";
  position: absolute;
}

.mainPageDiv button {
  background: transparent;
  color: #fff;
  border: 3px solid #fff;
  border-radius: 50px;
  padding: 0.8rem 2rem;
  font: 24px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
  letter-spacing: 2px;
}

.mainPageDiv .button-hand button:before {
  content: "👇";
  font-size: 60px;
  transform: scaleX(-1);
  right: 0px;
  top: -68px;
  animation: up-down 1s infinite;
}

.mainPageDiv .button-hand button .hands:before, .button-hand button .hands:after {
  content: "👇";
  font-size: 40px;
  opacity: 0;
  transition: 0.4s ease-in-out;
}

.mainPageDiv .button-hand button .hands:before {
  transform: rotate(-60deg);
  left: -45px;
  top: -10px;
}

.mainPageDiv .button-hand button .hands:after {
  transform: rotate(170deg);
  right: 30px;
  top: 50px;
}

.mainPageDiv .button-hand button:hover .hands:before {
  opacity: 1;
  left: -35px;
}

.mainPageDiv .button-hand button:hover .hands:after {
  opacity: 1;
  top: 40px;
}

@keyframes up-down {
  50% {
    margin-top: -20px;
  }
}